import { Component, OnInit } from '@angular/core'; import { Router, ActivatedRoute } from '@angular/router'; import { Subscription } from 'rxjs/Subscription';  import { Faixa_class_auditoria } from '../../../modelo/Faixa_class_auditoria'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual'; import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'faixa_class_auditoria-dados-details',   templateUrl: 'faixa_class_auditoria_dadosDetails.component.html' }) export class Faixa_class_auditoriaDadosDetailsComponent implements OnInit {    faixa_class_auditoria: Faixa_class_auditoria;    private sub: Subscription;    private errorMessage: string;    constructor(private route: ActivatedRoute, private pesquisaService: PesquisaService) { }    ngOnInit() {       this.sub = this.route.parent.params.subscribe(params => {         let o : ObjetoVirtual = new ObjetoVirtual();         o.classeJava = "br.com.primum.modelo.faixa_class_auditoria.Faixa_class_auditoria";                 let id = +params['id'];         this.faixa_class_auditoria = new Faixa_class_auditoria();         this.faixa_class_auditoria.cd_faixa_class_auditoria = id;         o.objetoJson = this.faixa_class_auditoria;          this.pesquisaService.efetuarPesquisa(o)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);       });   }    onEfetuarPesquisa(result : any) {     if (!result) {       this.errorMessage = 'Houve um erro ao pesquisa a faixa_class_auditoria. '; }      this.faixa_class_auditoria = result;   }    ngOnDestroy() {     this.sub.unsubscribe();   } }


